<template>
	<div>
		<div class="banner" @click='handleGallaryClick'>
			<img class="banner-img" :src="bannerImg" />
			<div class="banner-info">
				<div class="banner-title">{{this.sightName}}</div>
				<div class="banner-number">
					<span class="iconfont banner-icon">&#xe616;</span>
					{{this.bannerImgs.length}}
				</div>
			</div>
		</div>
		<fade-animation>
			<common-gallary
			 :imgs='bannerImgs' 
			 v-show='showGallary'
			 @close='handleGallaryclose'
			 ></common-gallary>
		</fade-animation>
	</div>
</template>

<script type="text/javascript">
	import CommonGallary from 'common/gallary/Gallary'
	import FadeAnimation from 'common/fade/FadeAnimation'
	export default {
		name: 'DetailBanner',
		props: {
			sightName: String,
			bannerImg: String,
			bannerImgs: Array
		},
		data () {
			return {
				showGallary: false
			}
		},
		methods: {
			handleGallaryClick () {
				this.showGallary = true
			},
			handleGallaryclose () {
				this.showGallary = false
			}
		},
		components: {
			CommonGallary,
			FadeAnimation
		}
	}
</script>

<style lang="stylus" scoped>
	.banner
		position: relative
		voerflow: hidden
		height: 0
		padding-bottom: 55%
		.banner-img
			width: 100%
		.banner-info
			display: flex
			position: absolute
			left: 0
			right: 0
			bottom: 0
			line-height: 26px
			color: #fff
			background: linear-gradient(top, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.6))
			.banner-title
				flex: 1
				font-size: 13px
				padding: 0 12px
			.banner-number
				padding: 8px
				height: 10px
				font-size: 13px
				line-height: 10px
				border-radius: 2px
				background: rgba(0, 0, 0, .6)
				.banner-icon
					font-size 13px
</style>


